// URL()
import config from '@/common/config.js'
import {
	getPageItem,
	getFilePath,
	getItemDetail,
	getImagePath,
	wxLogin,
	toPlaceOrder,
	getPayParams,
	getCart
} from '@/common/api.js'
import {
	getImageInfo,
	toPreviewImage,
	treeReform
} from '@/common/util.js'

// vmthisvmvuexuView
// https://uviewui.com/js/http.html#%E4%BD%95%E8%B0%93%E8%AF%B7%E6%B1%82%E6%8B%A6%E6%88%AA%EF%BC%9F
const install = (Vue, vm) => {
	// vm.$u.api(vmthisthis.$u.api)
	vm.$u.api = {
		getPageItem,
		getFilePath,
		getItemDetail,
		getImagePath,
		wxLogin,
		getImageInfo,
		toPlaceOrder,
		getPayParams,
		getCart
	};
	vm.$u.$api = config
	vm.$u.getUserInfo = config.getUserInfo
	vm.$u.getImageInfo = getImageInfo
	vm.$u.getImagePath = getImagePath
	vm.$u.toPreviewImage = toPreviewImage
	vm.$u.treeReform = treeReform
}

export default {
	install
}
